html,
#root,
body,
.App {
    height: 100%;
    width: 100%;
}

body {
    background-color: rgba($color: $sewcase-black, $alpha: 0.9);
    background-image: url("../images/pattern-60.png");
}

*,
:after,
:before {
    box-sizing: border-box;
    text-decoration: none;
    font-family: "Ubuntu";
}

ul {
    list-style-type: none;
}

a {
    color: $sewcase-white;
}
