.convention {
    &__container {
        align-items: center;
        color: $sewcase-white;
        display: flex;
        flex-direction: column;
        padding: 0;
        padding-top: 50px;
        @media (min-width: $md) {
            padding-top: 140px;
        }
    }

    &-box {
        display: flex;
        border-radius: 8px;
        box-shadow: 3px 3px 5px black;
        margin: 25px;
        overflow: hidden;
        @media (min-width: $md) {
            height: $sm;
            width: $md;
        }

        &__content {
            justify-content: center;
            align-items: center;
            width: 50%;
            @media (min-width: $md) {
                display: flex;
            }
        }

        &__image {
            display: none;
            align-items: center;
            width: 50%;
            @media (min-width: $md) {
                display: flex;
            }
            > img {
                background-color: $light-grey;
                border: 12px solid $sewcase-white;
                box-shadow: 3px 3px 4px black;
                max-height: 250px;
                position: absolute;
            }
            > img:nth-child(1) {
                transform: rotate(10deg) translate(40px, -10px);
                z-index: 1;
            }
            > img:nth-child(2) {
                transform: rotate(-7deg) translate(-10px, 40px);
                z-index: 2;
            }
            > img:nth-child(3) {
                transform: translate(20px);
                z-index: 3;
            }
        }
    }

    &__nav-box {
        border: 12px outset rgba($color: $sewcase-black, $alpha: 0.5);
        border-radius: 8px;
        color: $sewcase-black;
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 300px;
        width: 250px;
        text-align: center;
        z-index: 4;
        @media (min-width: $md) {
            display: flex;
        }
        &__link {
            background-color: $sewcase-yellow-light;
            border-radius: 8px;
            color: $sewcase-black;
            font-size: 16px;
            margin-top: auto;
            margin-bottom: auto;
            padding: 10px;
            transition: all 0.2s ease;
            &:hover {
                background-color: $sewcase-yellow;
                border: 2px solid rgba($color: $sewcase-black, $alpha: 0.5);
                font-size: 17px;
            }
        }
    }
}
